let SlidersArray = []

function sliderNews() {
    $(".news").each(function(index, el) {
        $(el).attr("data-slider-id", `${index}`)
        SlidersArray[index] = new Swiper(`.news[data-slider-id="${index}"] .swiper`, {
            slidesPerView: 2,
            spaceBetween: 30,
            pagination: {
                el: `.news[data-slider-id="${index}"] .swiper-pagination`,
                clickable: true,
            },
            breakpoints: {
                320: {
                    slidesPerView: 1,
                    spaceBetween: 15,
                },
                501: {
                    slidesPerView: 1.5,
                    spaceBetween: 15,
                },
                769: {
                    slidesPerView: 2,
                    spaceBetween: 30,
                }
            }
        })
    })
}

