$(document).ready(function() {
    sliderNews();
    sliderPartners();
    sliderFeedback();


//Menu
$('.burger').click(function () {
    $(this).toggleClass('burger--active')
    $('.header-bottom').toggleClass('header-bottom--active')
    $('body').toggleClass('fixed-body')
})

//Tabs
    $(".city-tab").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".city-tab").removeClass("city-tab--active")
        $(this).addClass("city-tab--active")

        $(this).parents(".page-frame").find(".city-tab__content").removeClass("city-tab__content--active")
        $(this).parents(".page-frame").find(`.city-tab__content[data-content-path="${path}"]`).addClass("city-tab__content--active")
    })

    //
    $(".language-tab").click(function () {
        let path = $(this).attr("data-tab2-path")
        $(this).siblings(".language-tab").removeClass("language-tab--active")
        $(this).addClass("language-tab--active")

        $(this).parents(".page-frame").find(".language-content").removeClass("language-content--active")
        $(this).parents(".page-frame").find(`.language-content[data-content2-path="${path}"]`).addClass("language-content--active")
    })

// Select
    $('.select__list-default').click(function () {
        $(this).parent('.select__wrap').addClass('select__wrap--active')
    })

    $('.select__list li').click(function () {
        var currentele = $(this).html()
        $(this).parents('.select__wrap').find('.select__current').html(currentele)
        $(this).parents('.select__wrap').removeClass('select__wrap--active')
        $(this).siblings(".select__list li").removeClass("li--active")
        $(this).addClass("li--active")
    })

    let selectLi = $('.select__list li')
    let selectWrap = $('.select__wrap')

    $(document).click(function (e) {
        if ( !selectLi.is(e.target) && selectLi.has(e.target).length === 0 &&
            !selectWrap.is(e.target) && selectWrap.has(e.target).length === 0

        ) {
            selectWrap.removeClass('select__wrap--active')
        }
    })

    //Accordion
    $(".visas__item-show").click(function () {
        let parent = $(this).parents(".visas__item")
        if($(parent).hasClass("visas__item--active")){
            $(".visas__item").removeClass("visas__item--active")
            $(".visas__item-hidden").slideUp()
        }else{
            $(".visas__item").removeClass("visas__item--active")
            $(parent).addClass("visas__item--active")
            $(".visas__item-hidden").slideUp()
            $(this).siblings(".visas__item-hidden").slideDown()
        }
    })


})

//Languages
const langButtons = document.querySelectorAll("[data-btn]");
const allLangs = ["ru", "kz"];
const currentPathName = window.location.pathname;
let currentLang =
    localStorage.getItem("language") || checkBrowserLang() || "ru";
let currentTexts = {};

const homeTexts = {
    "company": {
        ru: "О компании",
        kz: "Компания жайлы",
    },
};

// Проверка пути страницы сайта
function checkPagePathName() {
    switch (currentPathName) {
        case "/index.html":
            currentTexts = homeTexts;
            break;
        case "/another_page.html":
            currentTexts = anotherTexts;
            break;

        default:
            currentTexts = homeTexts;
            break;
    }
}
checkPagePathName();

// Изменение языка у текстов
function changeLang() {
    for (const key in currentTexts) {
        let elem = document.querySelector(`[data-lang=${key}]`);
        if (elem) {
            elem.textContent = currentTexts[key][currentLang];
        }
    }
}
changeLang();

// Вешаем обработчики на каждую кнопку
langButtons.forEach((btn) => {
    btn.addEventListener("click", (event) => {
        if (!event.target.classList.contains("language-btn--active")) {
            currentLang = event.target.dataset.btn;
            localStorage.setItem("language", event.target.dataset.btn);
            resetActiveClass(langButtons, "language-btn--active");
            btn.classList.add("language-btn--active");
            changeLang();
        }
    });
});

// Сброс активного класса у переданного массива элементов
function resetActiveClass(arr, activeClass) {
    arr.forEach((elem) => {
        elem.classList.remove(activeClass);
    });
}

// Проверка активной кнопки
function checkActiveLangButton() {
    switch (currentLang) {
        case "ru":
            document
                .querySelector('[data-btn="ru"]')
                .classList.add("language-btn--active");
            break;
        case "en":
            document
                .querySelector('[data-btn="en"]')
                .classList.add("language-btn--active");
            break;
        default:
            document
                .querySelector('[data-btn="ru"]')
                .classList.add("language-btn--active");
            break;
    }
}
checkActiveLangButton();

// Проверка языка браузера
function checkBrowserLang() {
    const navLang = navigator.language.slice(0, 2).toLowerCase();
    const result = allLangs.some((elem) => {
        return elem === navLang;
    });
    if (result) {
        return navLang;
    }
}

console.log("navigator.language", checkBrowserLang());


